@import '../../variables';

.home_container {
    display: grid;
    @media only screen and (max-width: 942px) {
        grid-template-columns: 3fr 8fr 1fr;
        height: 40vh;
    }
    grid-template-columns: 3fr 6fr 3fr; /* try auto-fill */
    height: 80vh;
}

.home-image {
    background: url(../../img/home.png) no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

.right {
    background: $grey;
}

.block {
    position: relative;
}
.moto {
    @media only screen and (max-width: 942px) {
        font-size: 30px;
        line-height: 45px;
    }

    @media only screen and (max-width: 320px) {
        font-size: 20px;
        line-height: 35px;
    }
    position: absolute;
    bottom: 10%;
    left: 10%;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 54px;
    line-height: 70px;
    /* or 130% */
    color: $grey;
    & span {
        background: $beige;
        color: $light;
    }
}
    
.cart {
    background-color: $grey;
    position: fixed;
    right: 1%;
    padding: 1%;
    border-radius: 3px;
    @media only screen and (max-width: 942px) {
        margin-top:30%;
        //width: 30px;
    }
    @media only screen and (max-width: 630px) {
        margin-top:50%;
    }
    /* @media only screen and (max-width: 350px) {
        margin-top:1%;
    } */
    margin-top:2%;
    //margin-right: 1%;
    text-align: center;
    color: $light;
    &_image {
        max-width: 40px;
        cursor: pointer;
    }
    &_total {
        display: block;
    }
    z-index: 3;
}